*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  background-color: #ffb3a7;
}

.navigation,
.shop-list__list,
.cart-list {
  display: flex;
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}
.cart-list {
  display: block;
}

img.shop-list-item__name {
  height: 300px;
  width: 400px;
}

.shop-list-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 100%;
  padding: 1.9rem 2.5rem;
  margin-bottom: 1rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.shop-list-item__url {
  width: 100%;
}

.shop-list-item__description {
  font-weight: 300;
  font-size: 14px;
  line-height: 12px;
  color: #000;
}
.shop-list-item__price {
  width: 100%;
  display: block;
  font-weight: 800;
  font-size: 20px;
}

.shop-list-item__content {
  font-size: 25px;
}

.shop-list-item__price {
  padding-top: 15px;
}

.button {
  cursor: pointer;
  position: relative;
  margin-top: 10px;
  width: 100%;
  padding: 9px 0;
  background-color: transparent;
  border: 1px solid #c93756;
  border-radius: 3rem;
  color: #c93756;
  font-size: 14px;
  font-weight: 600;
}
.button:hover {
  background-color: #c93756;
  color: #fff;
}
.button:focus {
  color: black;
}

.navigation {
  padding: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.navigation__item {
  font-size: 25px;
  border: 2px solid #000000;
  border-radius: 10px;
  background-color: #f4a460;
  color: #00bfff;
  margin-bottom: 5px;
}

a {
  text-decoration: none;
  color: black;
}

a.active {
  color: #00ffff;
}

.cart-list-item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 5px;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 23px;
}
.cart-list-item__header {
  display: flex;
  align-items: center;
}
.cart-list-item__image {
  width: 100px;
  height: 100px;
}
.cart-list-item__image img {
  width: 100%;
}

.cart-list-item__name {
  padding-left: 10px;
}
.button-cart {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #ffb3a7;
  border: none;
  font-size: 25px;
}
.cart-list-item__count {
  padding: 0 5px;
}
.button-cart {
  cursor: pointer;
}

.cart-list-item__delete {
  background-color: transparent;
  border: 1px solid #c93756;
  border-radius: 3rem;
  color: #c93756;
  font-size: 25px;
  padding: 5px;
}

.cart-list-item-sum {
  font-size: 25px;
}
